* {
    padding: 0;
    margin: 0;
}

body {
    min-width: 100px;
    min-height: 200px;
}

#err-background {
    animation: gradient-shift 6s ease-in-out infinite alternate;
    background-image: linear-gradient(75deg, #FFAB40, #B5A76F);
    bottom: 0;
    left: -50%;
    position: fixed;
    right: -50%;
    top: 0;
    z-index: -1;
}

@keyframes gradient-shift {
    0% {
        transform: translateX(-25%);
    }
    100% {
        transform: translateX(25%);
    }
}

#err-content {
    text-align: left;
    color: white;
    padding-top: 10vh;
    padding-left: 10vw;
    padding-right: 10vw;
}

#err-content h1 {
    font-size: 65px;
}

#err-content p {
    padding-top: 3vh;
    font-size: 20px;
}

#err-content a {
    text-decoration: none;
    color: #0097A7;
    font-weight: bold;
}